  --grey-lighter-color: #B7C0C7;
  --grey-light-color: #8E9BA4;
  --grey-color: #687782;
  --text-color: var(--grey-color);
  --title-color: #0F172A;
  --black-color: #000000;
  --white-color: #FFFFFF;
}

@font-face {
  font-family: "Rota";
  font-style: normal;
  font-weight: 600;
  src: local("Rota Bd"), url("/src/assets/fonts/rota-bold.woff2") format("woff2");
  font-display: optional;
}
:root {
  --body-font-family: "Manrope", sans-serif;
  --title-font-family: "Rota", sans-serif;
  --biggest-font-size: 2.25rem;
  --h1-font-size: 1.75rem;
  --h2-font-size: 1.5rem;
  --h3-font-size: 1.25rem;
  --h4-font-size: 1.125rem;
  --bigger-font-size: 1.125rem;
  --normal-font-size: 1rem;
  --small-font-size: 0.875rem;
  --smaller-font-size: 0.75rem;
}
@media screen and (min-width: 1024px) {
  :root {
    --biggest-font-size: 3rem;
    --h1-font-size: 2.5rem;
    --h2-font-size: 2rem;
    --h3-font-size: 1.5rem;
    --h4-font-size: 1.25rem;
    --bigger-font-size: 1.5rem;
  }
}

body {
  font-family: "Manrope", sans-serif;
  line-height: 1.8;
  font-size: 1rem;
  font-size: var(--normal-font-size);
}

h1,
.h1 {
  font-size: 1.75rem;
  font-size: var(--h1-font-size);
}

h2,
.h2 {
  font-size: 1.5rem;
  font-size: var(--h2-font-size);
}

h3,
.h3 {
  font-size: 1.25rem;
  font-size: var(--h3-font-size);
}

h4,
.h4 {
  font-size: 1.125rem;
  font-size: var(--h4-font-size);
}

h1, .h1, h2, .h2, h3, .h3, h4, .h4 {
  font-family: "Rota", sans-serif;
  font-family: var(--title-font-family);
  line-height: 1.5;
}

:root {
  --shadow-color: 39, 59, 73;
  --shadow-sm: 0 1px 2px 0 rgba(var(--shadow-color), 0.05);
  --shadow-normal: 0 1px 3px rgba(var(--shadow-color), 0.1),
                    0 1px 2px rgba(var(--shadow-color), 0.06);
  --shadow-md: 0 4px 6px -1px rgba(var(--shadow-color), 0.1),
                    0 2px 4px -1px rgba(var(--shadow-color), 0.06);
  --shadow-lg: 0 10px 15px -3px rgba(var(--shadow-color), 0.1),
                    0 4px 6px -2px rgba(var(--shadow-color), 0.05);
  --shadow-xl: 0 20px 25px -5px rgba(var(--shadow-color), 0.1),
                    0 10px 10px -5px rgba(var(--shadow-color), 0.04);
  --shadow-2xl: 0 25px 50px -12px rgba(var(--shadow-color), 0.25);
}
